
.logo {
  max-width: 279px;
  height: auto;
  display: block;
  border-bottom: 1px solid #eee;
}
.sidebar {
  position: fixed;
  background: #fff;
  height: 100%;
  width: 280px;
  transition: all 0.3s;
  z-index: 10;
  border-right: 1px solid #eee;
  overflow-y: auto;
  overflow-x: hidden;
}
.sidebar-open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.sidebar-link {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  outline: 0;
  text-align: center;
  padding: 8px 16px;
  border-bottom: 1px solid #eee;
  font-size: inherit;
}
.sidebar-link.active, .sidebar-link:hover {
  background-color: #3f51b5;
  color: #fff;
}
.btn {
  background: #fff;
  border: none;
  display: block;
  width: 100%;
  outline: none;
}
.back-btn {
  height: 50px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.18);
  font-size: 32px;
  display: none;
}
.back-btn:hover {
  background-color: #3f51b5;
  color: #fff;
}
.collapse-btn {
  padding: 8px 16px;
  border: 0;
  border-bottom: 1px solid #e0e0e0;
}
.collapse-btn:hover,
.collapse-btn.active { background: #eee;
}
.collapse-btn::after {
  content: "\25BE";
  float: right;
  color: #3f51b5;
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.collapse-btn.active::after { -webkit-transform: rotate(180deg); transform: rotate(180deg);
}
.collapse-panel {
  background-color: white;
  max-height: 0;
  overflow: hidden;
  font-size: 14px;
  transition: max-height 0.3s ease-out;
}
@media (max-width: 960px) {
.hide-md { display: none;
}
.back-btn { display: block;
}
}



